<template>

    <div class="hour-wrap">
        <span v-for="(h, index) of initItem.hours_arr" :key="index"
            :data-hour="h"

            class="hour"
            :class="{ 
                'sel': initHoverIndex === index,
                'cur': h == initItem.cur_hour
            }"
            
            @mouseover="updateHoverIndex(index)"
            @mouseleave="updateHoverIndex(false)"
        ></span>
    </div>

</template>

<script>
import { toRef } from 'vue'

export default {
    name: 'TimeZoneHours',
    props: ['item', 'hover_index'],
    emits: ['eventUpdateHoverIndex'],

    setup(props, context) {
        // 從 props 來的資料
        const initItem = toRef(props, 'item')
        const initHoverIndex = toRef(props, 'hover_index')
        


        // 方法

        // 更新目前 hover 的 hh index (子組件共用資料才能讓每一欄同時有高亮樣式)
        function updateHoverIndex(index) {
            context.emit('eventUpdateHoverIndex', index);
        }


        return {
            initItem,
            initHoverIndex,

            updateHoverIndex,
        }
    }
}
</script>