<template>

    <section class="header-wrap">

        <form class="header-inner-wrap">

            <fieldset>
                <label for="person-name">姓名</label>
                <input type="text" id="person-name" v-model="data.form_name" placeholder="請輸入姓名">
            </fieldset>

            <fieldset>
                <label for="timezone-continent">州</label>
                <select name="timezone-continent" id="timezone-continent" v-model="data.form_continent" @change="changeContinent()">
                    <option value="" selected>請選擇選項</option>
                    <option v-for="(continent, i) of data.select_continent_arr" :value="continent" :key="i">{{ continent }}</option>
                </select>
            </fieldset>

            <fieldset>
                <label for="timezone-country">國家</label>
                <select name="timezone-country" id="timezone-country" v-model="data.form_country" @change="changeCountry()">
                    <option value="" selected>請選擇選項</option>
                    <option v-for="(country, i) of data.select_country_arr" :value="country" :key="i">{{ country }}</option>
                </select>
            </fieldset>

            <fieldset>
                <label for="timezone-region">城市地區</label>
                <select name="timezone-region" id="timezone-region" v-model="data.form_region">
                    <option value="" selected>請選擇選項</option>
                    <option v-for="(region, i) of data.select_region_arr" :value="region" :key="i">{{ region }}</option>
                </select>
            </fieldset>

            <button type="button" @click="addPerson()" :disabled="!data.flag_required">新增成員</button>
        </form>

    </section>

</template>

<script>
import ct from 'countries-and-timezones'; // 套件 https://www.npmjs.com/package/countries-and-timezones
import { reactive, computed, onMounted, nextTick } from 'vue'

export default {
    name: 'TimeZoneHeader',
    emits: [
        'eventAddPerson'
    ],
    
    setup(props, context) {
        // 數據
        const data = reactive({
            aryIannaTimeZones: [], // 下拉式選單時區資料 (存放TimeZone物件)
            // #region
            // [
            //     {
            //         continent: '州',
            //         tzc: [
            //             {
            //                 country: '國家',
            //                 tz: [
            //                     {
            //                         timezones: '州/地區城市',
            //                         regions: '地區城市'
            //                     }
            //                 ]
            //             }
            //         ]
            //     }
            // ]
            // #endregion
            
            // 表單資料
            form_name: '', // 姓名輸入框
            form_continent: '', // 時區下拉選單-州
            form_country: '', // 時區下拉選單-國家
            form_region: '', // 時區下拉選單-城市地區
        })

        // 計算屬性

        // 必填欄位狀況
        data.flag_required = computed(()=>{
            return data.form_name != "" && data.form_continent != "" && data.form_country != "" && data.form_region != "";
        })


        // 計算屬性

        // 下拉式選單-州
        data.select_continent_arr = computed(()=>{
            let continent_arr = [];
            data.aryIannaTimeZones.forEach((item)=>{
                continent_arr.push(item.continent);
            });
            return continent_arr;
        })

        // 下拉式選單-國家
        data.select_country_arr = computed(()=>{
            let country_arr = [];
            if ( data.form_continent.length > 0 ) {
                let cur_continent = data.aryIannaTimeZones.filter((item)=>item.continent === data.form_continent)[0];
                cur_continent.tzc.forEach((item)=>{
                    country_arr.push(item.country);
                });
            }
            return country_arr.sort();
        })

        // 下拉式選單-時區
        data.select_region_arr = computed(()=>{
            let region_arr = [];
            if ( data.form_country.length > 0 ) {
                let cur_continent = data.aryIannaTimeZones.filter((item)=>item.continent === data.form_continent)[0];
                let cur_country = cur_continent.tzc.filter((item)=>item.country === data.form_country)[0];

                cur_country.tz.forEach((item)=>{
                    region_arr.push(item.region);
                });
            }
            return region_arr.sort();
        })



        // 方法

        async function getPlaceData() {
            //-------------------
            // Timezone Data
            //-------------------

            // 解析 timezone data
            let aryIanaTimeZones = Intl.supportedValuesOf('timeZone');
            let timezoneArr = aryIanaTimeZones.sort(); // 重新排序 (升序 A-Z)
   
            timezoneArr.forEach((timezone)=>{
                let [continent, ...rest] = timezone.split('/'); // 以 "/" 分割字串為陣列元素
                let remainder = rest.join('/'); // 排除第一個元素，其餘元素以 "/" 串接為一個新字串

                // 組合成 TimeZone 物件格式
                let tz = {
                    timezone: timezone,  // 包含時區 (州/地區城市)
                    region: remainder,  // 地區與城市
                }

                // 取得指定 timezone 的國家
                let countries = ct.getCountriesForTimezone(timezone);

                countries.forEach((country)=>{
                    // flag 是否已存在此州
                    let flag_continent = data.aryIannaTimeZones.some((item) => item.continent === continent);

                    // 已存在此洲物件
                    if ( flag_continent ) {

                        data.aryIannaTimeZones.forEach((obj)=>{
                            // 找出此洲的物件
                            if ( obj.continent === continent ) {
                                // flag 是否已存在此國家
                                let flag_country = obj.tzc.some((item) => item.country === country.name);

                                // 已存在此國家物件
                                if ( flag_country ) {
                                    obj.tzc.forEach((c_obj)=>{
                                        // 找出此國家的物件
                                        if ( c_obj.country === country.name ) {
                                            c_obj.tz.push(tz);
                                        }
                                    });

                                // 未存在此國家物件
                                } else {
                                    // 組合成 TimeZoneCountry 物件格式
                                    let tzc = {
                                        continent: continent,  // 洲
                                        country: country.name,  // 國家
                                        tz: [tz],  // TimeZone 物件
                                    }
                                    obj.tzc.push(tzc);
                                }
                            }
                        });

                    // 未存在此洲物件
                    } else {
                        // 組合成 TimeZoneCountry 物件格式
                        let tzc = {
                            continent: continent,  // 洲
                            country: country.name,  // 國家
                            tz: [tz],  // TimeZone 物件
                        }
                        
                        // 組合成 Place 物件格式
                        let place = {
                            continent: continent,  // 洲
                            tzc: [tzc],  // TimeZoneCountry 物件
                        }

                        // 新增此洲物件
                        data.aryIannaTimeZones.push(place); // 儲存資料
                    }
                });
            });
        }



        // 時區下拉選單-州 值改變
        function changeContinent() {
            data.form_country = ''; // 時區下拉選單-國家
            data.form_region = ''; // 時區下拉選單-城市地區
        }

        // 時區下拉選單-國家 值改變
        function changeCountry() {
            data.form_region = ''; // 時區下拉選單-城市地區
        }



        // 新增成員
        function addPerson() {

            // value 不為空值
            if ( data.flag_required ) {
                
                // 製作新物件
                let p = {
                    name: data.form_name,
                    timezone: `${data.form_continent}/${data.form_region}`
                };

                context.emit('eventAddPerson', p);  // 通知父組件新增

                // 清空欄位資料
                data.form_name = "";
                data.form_continent = "";
                data.form_country = "";
                data.form_region = "";
            }
        }


        // 生命週期鉤子

        // mounted
        onMounted(async ()=>{
            await getPlaceData(); // 取得資料

            nextTick(function () {
                // do stuff with the DOM

                
            })
        })



        return {
            data,

            changeContinent,
            changeCountry,
            addPerson,
        }
    }
}
</script>