<template>

    <div class="container">
        <div class="row">
            <div class="col-12">

                <section class="time-zone">
        
                    <!-- Header -->
                    <TimeZoneHeader
                        @eventAddPerson="addPerson"
                    />

                    <!-- Body -->
                    <TimeZoneBody :data="data"
                        @eventDelPerson="delPerson"
                        @eventSortPerson="sortPerson"
                    />

                </section>
                
            </div>
        </div>
    </div>

</template>
<script>
import { reactive, computed, onMounted, nextTick, onUnmounted } from 'vue'

// 匯入 HackTimer
import 'hacktimer/HackTimer.min.js';

import userData from "@/assets/data/timeZoneUserdata.json";

import TimeZoneBody from './TimeZoneBody.vue';
import TimeZoneHeader from './TimeZoneHeader.vue';

export default {
    name: 'TimeZone',
    components: {
        TimeZoneHeader,
        TimeZoneBody
    },

    setup() {
        // 數據
        const data = reactive({
            person_list: [], // 使用者清單 (存放Person物件)
            current_list: [], // 畫面時區清單 (存放CurrentList物件)
        })

        let timer = null;


        // 計算屬性
        
        // 製作 01 ~ 24 的陣列
        data.hours = computed(()=>{
            let arr = [];
            for ( let i=1 ; i <= 24 ; i++ ) {
                arr.push( i.toString().padStart(2, '0') );
            }
            return arr;
        })

        // 方法

        //-----------------------------------
        // Event
        //-----------------------------------

        // 取得資料
        async function getData() {

            //-------------------
            // Person Data
            //-------------------

            // 解析 person data
            let personsData = localStorage.getItem("persons"); // 從 localStorage 取得之前存的資料


            // 從 localStorage 取得
            if ( personsData ) {
                data.person_list = JSON.parse(personsData).persons;

            // 從 寫死 json 取得
            } else {
                
                userData.persons.forEach((userdata)=>{
                    let p = {
                        name: userdata.name,  // "王小明"
                        timezone: userdata.timezone  // "Asia/Taipei"
                    };
                    data.person_list.push(p);
                });
            }
        }


        //-----------------------------------
        // Render
        //-----------------------------------

        // 更新目前清單資料
        async function updateCurrentListData() {
            let date = new Date();
            data.current_list.splice(0, data.current_list.length); // 清空

            // 遍歷 person 資料
            data.person_list.forEach((person)=>{

                // 取得 person 所在時區的資料---

                // 世界時區: 2024/1/11 上午11:38:50 [GMT+8]
                let strTime = date.toLocaleString("zh-TW", {
                    timeZone: `${person.timezone}`,
                    timeZoneName: 'short' // e.g. GMT+1
                });
                // 目標: 取出 "GMT+8"
                let re_gmt = /\[(.+?)\]/g; // 取出中括號的內容
                let my_gmt_arr = re_gmt.exec(strTime); // [ "[GMT+8]", "GMT+8" ]
                let my_gmt = my_gmt_arr ? my_gmt_arr[1] : strTime.split(' ')[1];
                // false 的規則: ios 的 strTime 格式為 2024/1/11 GMT+8 上午11:38:50


                // 標準時間名稱 (中文): 2024/1/11 上午11:38:50 [台北標準時間]
                let strTime_ch = date.toLocaleString("zh-TW", {
                    timeZone: `${person.timezone}`,
                    timeZoneName: 'long' // e.g. 台北標準時間
                });
                // 目標: 取出 "台北標準時間"
                let re_gmt_ch = /\[(.+?)\]/g; // 取出中括號的內容
                let my_gmt_ch_arr = re_gmt_ch.exec(strTime_ch); // [ "[台北標準時間]", "台北標準時間" ]
                let my_gmt_ch = my_gmt_ch_arr ? my_gmt_ch_arr[1] : strTime_ch.split(' ')[1];
                // false 的規則: ios 的 strTime 格式為 2024/1/11 台北標準時間 上午11:38:50


                // 標準時間名稱 (英文): 1/11/2024, 11:38:50 AM Taipei Standard Time
                let strTime_en = date.toLocaleString("en-US", {
                    timeZone: `${person.timezone}`,
                    timeZoneName: 'long' // e.g. British Summer Time
                });
                // 目標: 取出 "Taipei Standard Time"
                let re_gmt_en = /(AM(.+)|PM(.+))/g; // 取出 AM 或 PM 後的字串
                let my_gmt_en_arr = re_gmt_en.exec(strTime_en); // ["...", "...", "undefined", "Taipei Standard Time"]
                let my_gmt_en = my_gmt_en_arr[2] ? my_gmt_en_arr[2] : my_gmt_en_arr[3];



                // 年份 (yyyy)
                let strTime_year = date.toLocaleString("en-US", {
                    timeZone: `${person.timezone}`,
                    hourCycle: 'h24', year: 'numeric'
                });

                // 月份 (mm)
                let strTime_month = date.toLocaleString("en-US", {
                    timeZone: `${person.timezone}`,
                    hourCycle: 'h24',
                    month: '2-digit'
                });

                // 日期 (dd)
                let strTime_day = date.toLocaleString("en-US", {
                    timeZone: `${person.timezone}`,
                    hourCycle: 'h24',
                    day: '2-digit'
                });

                // 星期幾 (www)
                let strTime_week = date.toLocaleString("zh-TW", {
                    timeZone: `${person.timezone}`,
                    weekday: 'long' // short: 週一 / long: 星期一
                });

                // 時間 (hh:mm:ss)
                let strTime_time = date.toLocaleString("en-US", {
                    timeZone: `${person.timezone}`,
                    hourCycle: 'h24',
                    hour: '2-digit', minute: '2-digit',
                    // second: '2-digit'
                });

                // 幾點 (hh)
                let strTime_hour = date.toLocaleString("en-US", {
                    timeZone: `${person.timezone}`,
                    hourCycle: 'h24',
                    hour: '2-digit'
                });


                // 時區對照 hh
                let hh_arr = [...data.hours]; // 複製一份 hh 陣列

                // 調整元素順序: 將 hh 放在中間 (索引=11位置)
                for (let k=0 ; k < 24 ; k++) {

                    // 若 hh 位於 索引=12的前方
                    if ( hh_arr.indexOf(strTime_hour) < 12 ) {
                        hh_arr.unshift( hh_arr.pop() ); // 將 最後一個元素 移至 開頭位置

                    // 若 hh 位於 索引=12的後方
                    } else if ( hh_arr.indexOf(strTime_hour) > 12 ) {
                        hh_arr.push( hh_arr.shift() ); // 將 第一個元素 移至 結尾位置
                    }
                }


                // 創建 CurrentList 物件
                let cl = {
                    person_name: person.name, // 姓名: "王小明"
                    person_location: person.timezone, // 時區: "Asia/Taipei"
                    cur_date: `${strTime_year}/${strTime_month}/${strTime_day}`, // 目前日期 (yyyy/mm/dd): "2024/01/11"
                    cur_week: strTime_week, // 目前星期幾 (www): "星期四"
                    cur_hour: strTime_hour, // 目前幾點 (hh): "10"
                    cur_time: strTime_time, // 目前時間 (hh:mm): "10:33"
                    gmt: my_gmt, // 世界時區: "GMT+8"
                    gmt_ch: my_gmt_ch, // 標準時間名 (ch): "台北標準時間"
                    gmt_en: my_gmt_en, // 標準時間名 (en): "Taipei Standard Time"
                    hours_arr: hh_arr, // 時間對照 hh array
                }
                
                // 更新資料
                data.current_list.push(cl);
            });
        }


        //-----------------------------------
        // Binding
        //-----------------------------------

        // 新增成員
        function addPerson(obj) {
            // 新增
            data.person_list.push(obj);
            updateCurrentListData(); // 更新表格資料

            // 將資料存到 localStorage
            localStorage.setItem("persons", JSON.stringify({ persons: data.person_list }));
        }

        // 刪除成員
        function delPerson(index) {
            let flag = window.confirm(`確定要刪除此人嗎？`);
            if ( !flag ) return;

            data.person_list.splice(index, 1); // 刪除指定索引的元素
            updateCurrentListData(); // 更新表格資料
            
            // 將資料存到 localStorage
            localStorage.setItem("persons", JSON.stringify({ persons: data.person_list }));
        }

        // 排序成員
        function sortPerson(index, type) {
            let index1 = index;
            let index2 = null;

            switch(type) {
                case 'up':
                    index2 = (index1 - 1) < 0 ? null : (index1 - 1);
                    break;
                case 'down':
                    index2 = (index1 + 1) > (data.person_list.length - 1) ? null : (index1 + 1);
                    break;
            }

            if ( index2 == null ) return;

            // 交換元素順序
            data.person_list[index1] = data.person_list.splice(index2, 1, data.person_list[index1])[0];

            updateCurrentListData(); // 更新表格資料
            
            // 將資料存到 localStorage
            localStorage.setItem("persons", JSON.stringify({ persons: data.person_list }));
        }

        


        // 生命週期鉤子

        // mounted
        onMounted(async ()=>{
            await getData(); // 取得資料
            await updateCurrentListData(); // 更新表格資料

            // 每分鐘執行
            timer = setInterval(()=>{
                updateCurrentListData(); // 更新表格資料
                console.log('每分鐘更新表格資料');
            }, 1000*60);


            nextTick(function () {
                // do stuff with the DOM
            })
        })

        // unmounted
        onUnmounted(()=>{
            clearInterval(timer);
        })



        return {
            data,

            addPerson,
            delPerson,
            sortPerson,
        }
    }
}
</script>
<style lang="scss">

</style>