<template>

    <section class="main-wrap">

        <table class="table table-wrap">
            <thead>
                <tr>
                    <th>目前時間</th>
                    <th>時區對照</th>
                    <th>地區/城市/GMT</th>
                    <th>時區</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) of initData.current_list" :key="index">

                    <!-- 目前時間 -->
                    <td>
                        {{ item.person_name }}<br>
                        <span class="time">{{ item.cur_time }}</span><br>
                        {{ item.cur_date }} {{ item.cur_week }}<br>

                        <div class="btn-wrap">
                            <button @click="sortPerson(index, 'up')">上移</button>
                            <button @click="sortPerson(index, 'down')">下移</button>
                            <button @click="delPerson(index)">刪除</button>
                        </div>
                    </td>

                    <!-- 時區對照 -->
                    <td>
                        <!-- 圖形對照表 -->
                        <TimeZoneHours 
                            :item="item" 
                            :hover_index="hover_index"
                            @eventUpdateHoverIndex="updateHoverIndex"
                        />
                    </td>

                    <!-- 地區/城市/GMT -->
                    <td>
                        {{ item.person_location }}<br>
                        {{ item.gmt }}
                    </td>

                    <!-- 時區 -->
                    <td>
                        {{ item.gmt_ch }}<br>
                        {{ item.gmt_en }}
                    </td>

                </tr>
            </tbody>
        </table>

    </section>

</template>
<script>
import { ref, toRef } from 'vue'

import TimeZoneHours from './TimeZoneHours.vue'

export default {
    name: 'TimeZoneBody',

    components: {
        TimeZoneHours
    },

    props: ['data'],
    emits: [
        'eventDelPerson',
        'eventSortPerson',
    ],

    setup(props, context) {
        // 從 props 來的資料
        const initData = toRef(props, 'data');

        // 數據
        let hover_index = ref(false);  // 目前 hover 到的 hh 的 index


        // 方法

        // 刪除成員
        function delPerson(index) {
            context.emit('eventDelPerson', index)
        }

        // 排序成員
        function sortPerson(index, type) {
            context.emit('eventSortPerson', index, type)
        }

        // 更新目前 hover 的 hh index (子組件共用資料才能讓每一欄同時有高亮樣式)
        function updateHoverIndex(index) {
            hover_index.value = index;
        }


        return {
            initData,
            hover_index,

            delPerson,
            sortPerson,
            updateHoverIndex,
        }
    }
}
</script>